.user_panel {
    width: 1296px;
    margin: 0 auto;
    margin-top: 45px;
    margin-bottom: 55px;

    .wrapper{
        display: flex;
        align-items: start;

        ul{
            width: 300px;
            height: 408px;
            border: 2px solid #E0E8FF;
            border-radius: 8px;
            margin-bottom: 45px;

            li{
                display: flex;
                align-items: center;
                width: 100%;
                height: 68px;
                padding: 24px;
                border-bottom: 2px solid #F4F7FF;
                font-weight: 400;
                font-size: 16px;
                transition: .3s;

                &:hover{
                    background-color: rgba(0, 0, 0, 0.1);
                }

                p{
                    color: #5C596D;
                }
            }
            
            .first {
                background-color: #E0E8FF;
                font-weight: 400;
                font-size: 16px;
                color: #1D1A2F;

                p{
                    color: #1D1A2F;
                }
            }
        }

        form{
            width: 410px;
            margin-left: 32px;

            h1{
                font-weight: 500;
                font-size: 20px;
                color: #1D1A2F;
                margin-bottom: 24px;
            }

            .name{
                display: flex;
                flex-direction: column;
                margin-bottom: 24px;

                label{
                    margin-bottom: 4px;
                    font-weight: 400;
                    font-size: 14px;
                    color: #5C596D;
                }

                input{
                    width: 100%;
                    padding: 14px 16px;
                    font-weight: 400;
                    font-size: 16px;
                    color: #1D1A2F;
                    background: #F7F7F7;
                    border-radius: 4px;
                    border: none;
                    outline: none;
                }
            }
            .btn_control{
                width: 100%;
                display: flex;
                align-items: center;

                .refresh{
                    width: 156px;
                    height: 48px;
                    background: #1B5BF7;
                    border-radius: 4px;
                    border: none;
                    outline: none;
                    font-weight: 400;
                    font-size: 15px;
                    color: #fff;
                    margin-right: 12px;
                }

                .resolve{
                    width: 156px;
                    height: 48px;
                    background: #F7F7F7;
                    border-radius: 4px;
                    border: none;
                    outline: none;
                    font-weight: 400;
                    font-size: 15px;
                    color: #1B5BF7;
                }
            }
        }
    }
}