.basket{
    width: 1296px;
    margin: 0 auto;
    position: relative;

    div{
        .title{
            width: 846px;
            display: flex;
            align-items: center;
            justify-content: space-between;
    
            .main{
                display: flex;
                align-items: center;
                
                .trash{
                    font-weight: 700;
                    font-size: 20px;
                    margin-right: 21px;
                    color: #000;
                }
        
                .counter{
                    font-weight: 400;
                    font-size: 14px;
                    color: #5C596D;
                }
            }
            p{
                font-weight: 400;
                font-size: 16px;
                color: #FF5959;
            }
            
        }
    
        .wrapper{
            display: flex;
            // align-items: center;
            justify-content: space-between;
    
            .wrapper_left{
                width: 846px;
                display: flex;
                align-items: flex-start;
                justify-content: space-between;
                border-bottom: 1px solid #9692B0;
                padding-bottom: 45px;
                margin-top: 45px;
    
                .left_box{
                    display: flex;
    
                    img{
                        width: 105px;
                        height: 128px;
                        object-fit: cover;
                    }
    
                    .left_desc{
                        margin-left: 15px;
                        .info{
                            margin-bottom: 48px;
    
                            .name{
                                font-weight: 500;
                                font-size: 16px;
                                margin-bottom: 8px;
                            }
    
                            .desc{
                                font-weight: 400;
                                font-size: 15px;
                                color: #9692B0;
                            }
                        }
    
                        .controls{
                            display: flex;
                            align-items: center;
    
                            div{
                                display: flex;
                                align-items: center;
                                color: #9692B0;
                                margin-right: 35px;
                                span{
                                    margin-left: 10px;
                                }
                            }
                        }
                    }
                }
                .right_box{
                    display: flex;
                    align-items: flex-end;
                    flex-direction: column;
    
                    .counts{
                        display: flex;
                        align-items: center;
                        margin-bottom: 32px;
    
                        button{
                            display: flex;
                            padding: 15px 20px;
                            border: none;
                            outline: none;
                            border-radius: 50%;
                            color: #5C596D;
                        }
                        span{
                            margin: 0 16px;
                            font-weight: 400;
                            font-size: 20px;
                        }
                    }
                    
                    .price{
                        font-weight: 400;
                        font-size: 20px;
                    }
                }
            }
    
            
        }
    }

    .wrapper_right{
        width: 338px;
        padding: 32px 27px;
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.07);
        border-radius: 8px;
        position: absolute;
        top: 5%;
        right: 3px;
        background-color: #fff;
        z-index: 99;

        .total{
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-weight: 700;
            font-size: 20px;
            margin-bottom: 24px;
        }

        .products{
            font-weight: 400;
            font-size: 15px;
            margin-bottom: 16px;
        }

        .discount{
            font-weight: 400;
            font-size: 15px;
            margin-bottom: 16px;

            span{
                font-weight: 500;
            }
        }
        .delivery{
            font-weight: 400;
            font-size: 15px;
            margin-bottom: 24px;

            a{
                font-weight: 400;
                font-size: 15px;
                text-decoration: none;
                color: #1B5BF7;
                margin-left: 8px;
            }
        }

        button{
            width: 100%;
            padding: 11px 0;
            font-weight: 400;
            font-size: 14px;
            background-color: #1B5BF7;
            border: none;
            outline: none;
            margin-bottom: 27px;
            color: #fff;
            border-radius: 4px;
        }
        .text{
            color: #9692B0;
            font-weight: 400;
            font-size: 15px;
            line-height: 20px;
        }
    }
}