.sign_phone{
    width: 100%;
    height: 100vh;
    background-color: #0000004d;
    // position:relative;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index: 99;
    overflow-y: hidden;

    form{
        background: #FFFFFF;
        border-radius: 8px;
        padding: 32px 64px;
        position: relative;
        p{
            margin-top: 24px;
        }

        input{
            width: 100%;
        }

        .error{
            color: red;
            font-size: 14px;
            font-weight: 400;
        }

        .close{
            position: absolute;
            top: 2%;
            width: 12px;
            height: 12px;
            background-color: #FFFFFF;
        }
    }
}