.phone_page{
    width: 1296px;
    margin: 0 auto;

    .top_bar{
        display: flex;
        align-items: center;
        margin-bottom: 25px;

        p{
            font-weight: 400;
            font-size: 16px;
            padding-right: 8px;
            border-right: 1px solid #9692B0;
        }

        span{
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            margin-left: 8px;
            color: #9692B0;
        }
    }

    .wrapper{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;

        .left_part{
            width: 274px;
            
            h1{
                font-weight: 700;
                font-size: 24px;
                margin-bottom: 24px;
            }

            .side_bar{
                width: 100%;
                background: #F7F7F7;
                border-radius: 8px;
                padding: 32px;

                .name{
                    font-weight: 700;
                    font-size: 20px;
                    margin-bottom: 32px;
                    color: #5C596D;
                }
                .filter{
                    font-weight: 400;
                    font-size: 16px;
                    margin-bottom: 16px;
                }

                .prices_filter{
                    display: flex;
                    align-items: center;
                    margin-bottom: 24px;

                    div{
                        width: 96px;
                        margin-right: 8px;
                        
                        label {
                            font-weight: 400;
                            font-size: 13px;
                            color: #9692B0;
                        }

                        input{
                            width: 100%;
                            padding: 15px;
                            background-color: #EAE9EF;
                            border-radius: 4px;
                            border: none;
                            outline: none;
                        }
                    }
                }

                .brands{
                    display: flex;
                    flex-direction: column;

                    h2{
                        font-weight: 400;
                        font-size: 16px;
                        margin-bottom: 16px;
                    }

                    .brends_names{
                        // display: flex;
                        // align-items: flex-start;
                        // flex-direction: column;
                        margin-bottom: 24px;

                        .brend_name{
                            display: flex;
                            align-items: center;

                            .checkbox{
                                margin-right: 8px;
                            }

                            h3{
                                color: #5C596D;
                                font-weight: 400;
                                font-size: 15px;
                            }
                        }
                    }
                }
            }
        }

        .right_part{
            width: 964px;

            ul{
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            .products{
                margin-top: 26px;
                width: 100%;
                margin-bottom: 117px;
                ul{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    
                    li{
                        width: 300px;
                        padding: 25px;
                        box-shadow: 0px 0px 75px rgba(0, 0, 0, 0.08);
                        border-radius: 8px;
                        position: relative;
                        margin-bottom: 32px;
                        

                        .hover{
                            position: absolute;
                            left: 20%;
                            top: 64px;
                            color: #000;
                            padding: 12px 21px;
                            font-weight: 500;
                            font-size: 16px;
                            background: #fff;
                            box-shadow: 0px 0px 43px rgba(0, 0, 0, 0.15);
                            border-radius: 25px;
                            display: none;
                            transition: 0.5s all;
                            cursor: pointer;
                        }
            
                        .product-name{
                            margin-top: 17px;
                            font-weight: 400;
                            font-size: 16px;
                        }
                        
                        .price{
                            margin-top: 16px;
                            font-weight: 600;
                            font-size: 20px;
                            margin-bottom: 8px;
                        }
            
                        .debt_price{
                            font-weight: 400;
                            font-size: 16px;
                        }
            
                        .stars{
                            display: flex;
                            align-items: center;
                            margin: 15px 0;
                        }
            
                        .btn_control{
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
            
                            button {
                                display: flex;
                                align-items: center;
                                padding: 11px 14px;
                                background-color: #1B5BF7;
                                color: #fff;
                                border-radius: 4px;
                                border: none;
                                outline: none;
            
                                img{
                                    margin-right: 10px;
                                }
                            }
            
                            .one{
                                background-color: #F6F6F6;
                                color: #5C596D;
                                padding: 11px 34px;
                            }
                            .two{
                                background-color: #E0E8FF;
                                color: #1B5BF7;
                                padding: 11px 34px;
                            }
            
                            .mark{
                                display: flex;
                                align-items: center;
                                background-color: transparent;
            
                                img{
                                    margin-right: 20px;
                                }
            
                                .like{
                                    color: #1B5BF7;
            
                                    &:focus .liked{
                                        opacity: 1;
                                    }
                                }
            
                                .liked{
                                    opacity: 0;
                                }
                            }
                        }
                    }
                }

            }
        }
    }
}