.shop{
    width: 1296px;
    margin: 0 auto;
    margin-bottom: 42px;

    .shop_list{
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        
        .shop_item{
            width: 100%;
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            padding: 15px;
            border: 2px solid #F2F2F2;
            border-radius: 8px;
            margin-bottom: 12px;

            .name{
                font-weight: 400;
                font-size: 20px;
            }
        }
    }

    .maps{
        width: 100%;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-top: 37px;

        .left_side{
            display: flex;
            align-items: flex-start;
            flex-direction: column;

            div{
                display: flex;
                align-items: flex-start;
            }

            .img{
                margin-bottom: 24px;
            }

            .location{
                margin-bottom: 40px;
               img{
                   width: 24px;
                   height: 24px;
                   margin-right: 19px;
               }

               p{
                    width: 222px;
                    margin-right: 19px;
                    font-weight: 400;
                    font-size: 16px;
               }
            }

            .behind{
                margin-bottom: 40px;
                img{
                    width: 24px;
                    height: 24px;
                    margin-right: 19px;
                }
                p{
                    margin-right: 19px;
                    font-weight: 400;
                    font-size: 16px;
                }
            }

            .time{
                margin-bottom: 40px;
                img{
                    width: 24px;
                    height: 24px;
                    margin-right: 19px;
                }
                p{
                    font-weight: 500;
                    font-size: 20px;
                }
            }

            .contact{
                img{
                    width: 24px;
                    height: 24px;
                    margin-right: 19px;
                }
                p{
                    font-weight: 500;
                    font-size: 20px;
                }
            }
        }
    }
}