.sync{
    width: 1296px;
    margin: 0 auto;
    margin-bottom: 40px;

    table, th, td {
        border: 1px solid grey;
        text-align: center;
    }
    table{
        width: 100%;
        border-collapse: collapse;
        -moz-border-radius:10px;
    -webkit-border-radius:10px;
    border-radius:10px
    }
    
    th{
        padding: 11px 43px;
        color: #5C596D;
        font-weight: 400;
        font-size: 14px;
    }

    td{
        height: 90px;
    }

    .price{
        font-weight: 700;
        font-size: 14px;
    }
}