.login{
    width: 100%;
    height: 100vh;
    background-color: #0000004d;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index: 99;

    form{
        width: 552px;
        // height: 668px;
        display: flex;
        align-items: center;
        flex-direction: column;
        border: 1px solid #fff;
        padding: 10px 71px;
        background-color: #fff;
        border-radius: 8px;

        .top_part{
            width: 50%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: relative;

            h1{
                font-weight: 700;
                font-size: 24px;
                margin-bottom: 24px;
            }

            button{
                position: absolute;
                right: -70%;
                top: 20%;
                width: 10px;
                height: 10px;
                padding: 0;
                margin: 0;
                background-color: transparent;
                font-weight: 700;
                cursor: pointer;
                border: none;
                outline: none;
                color: #0000004d;

                img{
                    width: 32px;
                    height: 32px;
                }
            }
        }


        input{
            width: 410px;
            font-weight: 400;
            font-size: 16px;
            margin-bottom: 12px;
            padding: 14px 16px;
            background: #F7F7F7;
            border-radius: 4px;
            border: none;
            outline: none;
        }

        button{
            width: 410px;
            color: #9692B0;
            font-weight: 500;
            font-size: 16px;
            background: #EAE9EF;
            border-radius: 4px;
            padding: 14px 124px;
            border: none;
            outline: none;
        }
        .checkbox{
            display: flex;
            margin-top: 24px;

            input{
                width: 23px;
                height: 23px;
                margin-right: 24px;
                border: 2px solid #1B5BF7;
                box-sizing: border-box;
                border-radius: 4px;
            }

            p{
                font-weight: 400;
                font-size: 16px;
            }

            a{
                color: #1B5BF7;
            }
        }

        h2{
            font-weight: 400;
            font-size: 16px;
            margin-bottom: 24px;
        }

        .pop_up{
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 24px;

            .p{
                display: flex;
                padding: 14px 62px;
                background-color: #F7F7F7;
                border-radius: 4px;
            }
        }
        p{
            a{
                color: #1B5BF7;
            }
        }
    }
}