.single_product{
    width: 100%;
    height: 110vh;
    position: absolute;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 99;

    .wrapper{
        width: 781px;
        height: 749px;
        padding: 40px;
        background-color: #fff;
        border-radius: 8px;
        // padding: 24px;
        display: flex;
        flex-direction: column;

        .wrap_top{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 24px;

            .top_title{
                display: flex;
                flex-direction: column;

                h1{
                    font-weight: 700;
                    font-size: 24px;
                    margin-bottom: 24px;
                }

                .top_desc{
                    display: flex;
                    align-items: center;

                    .stars{
                        display: flex;
                        padding-right: 16px;
                        border-right: 3px solid #f7f7f7;
                    }

                    .comment{
                        border-right: 3px solid #f7f7f7;
                        padding: 0 16px;
                        font-weight: 400;
                        font-size: 16px;
                        color: #5C596D;
                    }
                    .on_sale{
                        font-weight: 400;
                        font-size: 16px;
                        color: #5C596D;
                    }
                }
            }

            button{
                border: none;
                outline: none;
                background-color: #fff;
                font-size: 20px;
                font-weight: 600;
            }
        }

        .wrap_center{
           width: 100%;
           display: flex;
           align-items: center;
           justify-content: space-between; 

           .center_left{
               padding: 30px 80px;
               background: #F7F7F7;
                border-radius: 8px;
                margin-right: 30px;
           }

           .center_right{
               text-align: left;
               width: 216px;

               .price{
                font-weight: 700;
                font-size: 24px;
                margin-bottom: 24px;
               }

               .colors{
                   display: flex;
                   flex-direction: column;

                   p{
                    font-weight: 400;
                    font-size: 16px;
                    margin-bottom: 8px;
                    color: #9692B0;

                   }

                   .color{
                       display: flex;
                       align-items: center;
                       margin-bottom: 24px;

                       .red{
                           width: 30px;
                           height: 30px;
                           background-color: red;
                           border-radius: 50%;
                           margin-right: 10px;
                           transition: 0.3s;

                           &:hover{
                            padding: 5px;
                            border: 1px solid red;
                        }
                       }
                        .grey{
                            width: 30px;
                            height: 30px;
                            background-color: grey;
                            border-radius: 50%;
                            margin-right: 10px;
                            transition: 0.3s;

                            &:hover{
                                padding: 5px;
                                border: 1px solid grey;
                            }
                            }
                            .blue{
                                width: 30px;
                                height: 30px;
                                background-color: blue;
                                border-radius: 50%;
                                transition: 0.3s;

                                &:hover{
                                    padding: 5px;
                                    border: 1px solid blue;
                                }
                            }
                   }

                }

                .storage{
                    margin-bottom: 24px;

                 .store{
                     color: #9692B0;
                     font-weight: 400;
                     font-size: 16px;
                     margin-bottom: 8px;
                 }
                .toggle{
                    border: 1px solid #E6E5EC;
                    color: #9692B0;
                }
                 .toggle:focus{
                     background-color: #f2f2f2;
                     color: #5C596D;
                 }
                }

                .sims{
                    margin-bottom: 24px;

                    .sim{
                        margin-bottom: 8px;
                        color: #9692B0;
                        font-weight: 400;
                        font-size: 16px;
                    }
                    .toggle{
                        border: 1px solid #E6E5EC;
                        color: #9692B0;
                    }
                     .toggle:focus{
                         background-color: #f2f2f2;
                         color: #5C596D;
                     }  
                }

                .trash{
                    font-weight: 400;
                    font-size: 14px;
                    margin-left: 0;
                }

                .btn{

                    &:hover img{
                        transform: rotate(14.95deg);
                        transition: 0.3s all;
                    }

                    img{
                        margin-right: 12px;
                    }
                }
           }
        }

        .wrap_bottom{
            display: flex;
            flex-direction: column;

            .bot_images{
                display: flex;
                align-items: center;

                img{
                    width: 86px;
                    height: 86px;
                    margin-right: 31px;
                    padding: 5px;
                    border-radius: 4px;
                    transition: 0.3s;
                    
                    &:hover{
                        border: 1px solid blue;
                    }
                }
            }

            .product_info{
                text-align: center;
                margin-top: 33px;
            }
        }
    }
}

.hide{
    display: none;
}
.show{
    display: block;
}