.products{
    width: 1296px;
    margin: 0 auto;

    h1{
        font-weight: 700;
        font-size: 24px;
        margin-bottom: 24px;
    }

    .product_list{
        display: flex;
        align-items: center;
        justify-content: space-between;
        list-style: none;

        .product_item{
            width: 300px;
            height: 460px;
            padding: 25px;
            box-shadow: 0px 0px 75px rgba(0, 0, 0, 0.08);
            border-radius: 8px;
            position: relative;

            &:hover{
                border: 1px solid #1B5BF7;
            }
            
            &:hover .hover{
                display: block;
            }
            .hover{
                position: absolute;
                left: 20%;
                top: 64px;
                color: #000;
                padding: 12px 21px;
                font-weight: 500;
                font-size: 16px;
                background: #fff;
                box-shadow: 0px 0px 43px rgba(0, 0, 0, 0.15);
                border-radius: 25px;
                transition: 0.5s all;
                cursor: pointer;
                display: none;
            }
            

            .product-name{
                margin-top: 17px;
                font-weight: 400;
                font-size: 16px;
            }
            
            .price{
                margin-top: 16px;
                font-weight: 600;
                font-size: 20px;
                margin-bottom: 8px;
            }

            .debt_price{
                font-weight: 400;
                font-size: 16px;
            }

            .stars{
                display: flex;
                align-items: center;
                margin: 15px 0;
            }

            .btn_control{
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;

                button {
                    display: flex;
                    align-items: center;
                    padding: 11px 14px;
                    background-color: #1B5BF7;
                    color: #fff;
                    border-radius: 4px;
                    border: none;
                    outline: none;

                    &:hover img{
                        transform: rotate(14.95deg);
                        transition: 0.3s;
                    }
                    
                    img{
                        width: 13px;
                        height: 13px;
                        object-fit: cover;
                        margin-right: 8px;
                        border: none;
                        color: #fff;
                        // border: 1px solid #000;
                    }
                }


                .one{
                    background-color: #F6F6F6;
                    color: #5C596D;
                    padding: 11px 34px;
                }
                .two{
                    background-color: #E0E8FF;
                    color: #1B5BF7;
                    padding: 11px 34px;
                }

                .mark{
                    display: flex;
                    align-items: center;
                    background-color: transparent;

                    img{
                        margin-right: 20px;
                    }

                    .like{
                        color: #1B5BF7;

                        &:focus .liked{
                            opacity: 1;
                        }
                    }

                    .liked{
                        opacity: 0;
                    }
                }
            }
        }
    }
}

