.first_box {
  width: 100%;
  margin: 0 auto;

  .first-list {
    width: 1296px;
    margin: 0 auto;
    padding: 11px 0;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .first_item {
      display: flex;
      align-items: center;

      span {
        margin-right: 32px;
        font-weight: 400;
        font-size: 14px;
        color: #5c596d;
      }
    }

    .second_item {
      display: flex;
      align-items: center;

      .contact {
        display: flex;
        align-items: center;
        margin-left: 32px;

        img {
          width: 15px;
          height: 15px;
          margin-right: 6px;
        }

        select {
          border: none;
          font-weight: 400;
          font-size: 14px;
        }

        .location {
          appearance: none;
          width: 100px;
          border: none;
          

      }

        span {
          font-weight: 400;
          font-size: 14px;
          color: #5c596d;
        }

        .bottom {
          width: 10px;
          height: 10px;
          margin-left: 5px;
        }
      }
    }
  }
}

.second_box {
  width: 1296px;
  margin: 0 auto;

  .second_list {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    list-style: none;
    padding: 12px 0;

    .second_item-2 {
      .second_item-form {
        display: flex;
        align-items: center;
        background: #f7f7f7;
        border-radius: 4px;
        padding: 15px;
        margin-left: 15px;
        position: relative;

        input {
          width: 677px;
          border: none;
          background: #f7f7f7;
          outline: none;
          margin-left: 15px;
          padding-right: 15px;
        }

        button{
          width: 24px;
          height: 24px;
          border: none;
          outline: none;

          img{
            width: 100%;
            height: 100%;
          }

          &:hover p{
            display: block;
          }
          p {
            font-weight: 500;
            font-size: 14px;
            background: #ffffff;
            border-radius: 2px;
            padding: 8px;
            position: absolute;
            top: 80%;
            right: -4%;
            display: none;
          }
        }
      }
    }

    .second_item-3 {
      display: flex;
      align-items: center;
      margin: 0 auto;

      .second_item-box {
        display: flex;
        align-items: center;
        flex-direction: column;
        margin-left: 24px;
        cursor: pointer;
        transition: 0.3s;
        position: relative;
        color: #5c596d;

        &:hover {
          color: #1b5bf7;
        }

        .link {
          display: flex;
          align-items: center;
          flex-direction: column;
          color: #5c596d;

          &:hover {
            color: #1b5bf7;
          }
        }

        .notification {
          position: absolute;
          top: -35%;
          right: 15%;
          width: 21px;
          height: 21px;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 50%;
          color: #fff;
          font-weight: 500;
          font-size: 15px;
          background-color: #ff5959;
        }
      }
    }
  }
}

.third_box {
  width: 100%;
  margin: 0 auto;
  padding: 10px;
  background-color: #f8f8f8;
  margin-bottom: 32px;

  .third_list {
    width: 1296px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    list-style: none;

    .third_item {
      span {
        font-weight: 400;
        font-size: 15px;
        color: #5c596d;

        &:hover {
          color: #000;
        }
      }
    }
  }
}
