.comment{
    width: 1296px;
    margin: 0 auto;
    margin-bottom: 72px;

    h1{
        font-weight: 700;
        font-size: 24px;
        margin-bottom: 16px;
    }

    p{
        width: 514px;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: #5C596D;
        margin-bottom: 22px;
    }

    form{
        width: 520px;
        display: flex;
        align-items: flex-start;
        flex-direction: column;

        div{
            width: 520px;
            display: flex;
            flex-direction: column;
            margin-bottom: 32px;

            label{
                font-size: 16px;
                line-height: 20px;
                margin-bottom: 8px;
            }

            input{
                padding: 14px 16px;
                font-weight: 400;
                font-size: 16px;
                color: #9692B0;
                background-color: #F7F7F7;
                border-radius: 4px;
                border: none;
                outline: none;
            }

            select{
                padding: 14px 16px;
                font-weight: 400;
                font-size: 16px;
                color: #9692B0;
                background-color: #F7F7F7;
                border-radius: 4px;
                border: none;
                outline: none;
            }

            textarea{
                width: 520px;
                height: 146px;
                padding: 16px;
                resize: none;
                font-weight: 400;
                font-size: 16px;
                color: #9692B0;
                background-color: #F7F7F7;
                border-radius: 4px;
                border: none;
                outline: none;
            }
        }

        .new_comments{
            margin-bottom: 0;
        }

        button{
            margin-top: 16px;
            width: 100%;
            padding: 18px 0;
            font-weight: 400;
            font-size: 16px;
            background: #1B5BF7;
            border-radius: 4px;
            color: #fff;
            border: none;
            outline: none;
        }
    }
}