.order {
    width: 1296px;
    margin: 0 auto;

    h1 {
        margin-top: 46px;
        font-weight: 700;
        font-size: 20px;
        color: #1D1A2F;
        margin-bottom: 32px;
    }

    .main_wrapp{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }

    .form {
        width: 520px;
        display: flex;
        flex-direction: column;

        .username, .telephone, .place, .location {
            display: flex;
            flex-direction: column;
            margin-bottom: 32px;

            label {
                font-weight: 500;
                font-size: 16px;
                color: #5C596D;
                margin-bottom: 8px;
            }

            input {
                background: #F7F7F7;
                border-radius: 4px;
                padding: 14px 16px;
                word-break: break-all;
                font-weight: 400;
                font-size: 16px;
                color: #9692B0;
                border: none;
                outline: none;
            }

            .error{
                color: red;
                font-size: 14px;
                font-weight: 400;
            }

            .tel {
                color: #1D1A2F;
            }

            select {
                padding: 14px 16px;
                background: #F7F7F7;
                border-radius: 4px;
                font-weight: 400;
                color: #9692B0;
                font-size: 16px;
            }

            iframe {
                width: 519px;
                height: 209px;
                resize: none;
            }
        }

        .location {
            p {
                color: #5C596D;
                margin-bottom: 16px;
            }
        }

        .payment {
            // width: 100%;

            p{
                font-weight: 500;
                font-size: 16px;
                color: #5C596D;
            }

            .payment_images {
                // width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                
                div {
                    width: 33.5%;
                    height: 70px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-top: 20px;
                    border: 1px solid #9692B0;
                    display: flex;
                    border: 1px solid #9692B0;
                    border-radius: 4px;

                    img {
                        width: 81px;
                        height: 30px;
                        object-fit: contain;
                    }
                }
            }
        }

        .delivery{
            width: 100%;
            p{
                color: #5C596D;
                margin-bottom: 16px; 
                font-weight: 500;
                font-size: 16px;
                color: #5C596D;

            }
            .dilevery_wrap{
                // width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;

                .first_way, .second_way{
                    width: 253px;
                    height: 56px;
                    border: 1px solid #9692B0;
                    display: flex;
                    align-items: center;
                    padding-left: 15px;
                    border-radius: 4px;

                    img{
                        width: 24px;
                        height: 24px;
                    }

                    p{
                        margin-top: 15px;
                        font-weight: 500;
                        font-size: 16px;
                        color: #5C596D;
                        margin-left: 18px;
                    }
                }
            }
        }

        .add_com{
            width: 100%;
            margin-bottom: 24px;

            p{
                color: #5C596D;
                margin-bottom: 16px; 
                font-weight: 500;
                font-size: 16px;
                color: #5C596D;
            }

            textarea{
                width: 100%;
                height: 210px;
                resize: none;
                color: #5C596D;
                font-weight: 500;
                font-size: 16px;
                padding: 16px;
                border: 1px solid #9692B0;
                border-radius: 4px;
            }
        }

        .sam{
            font-weight: 400;
            font-size: 15px;
            color: #5C596D;
            margin-bottom: 48px;

            a{
                color: #1B5BF7;
            }
        }

        .end{
            margin-bottom: 73px;
            background: #1B5BF7;
            width: 100%;
            padding: 18px 0;
            border-radius: 4px;
            color: #fff;
            border: none;
        }
    }

    .right_side{
        width: 521px;
        // height: 380px;
        background: #FFFFFF;
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.07);
        border-radius: 8px;
        padding: 32px;

        h2{
            font-weight: 500;
            font-size: 20px;
            margin-bottom: 32px;
        }

        ul{
            display: flex;
            flex-direction: column;

            li{
                display: flex;
                align-items: flex-start;
                justify-content: space-between;
                margin-bottom: 24px;

                div{
                    display: flex;
                    padding-bottom: 24px;
                    border-bottom: 2px solid #F2F2F2;

                    p{
                        font-weight: 400;
                        font-size: 16px;
                        color: #5C596D;
                    }

                    span{
                        font-weight: 400;
                        font-size: 16px;
                        color: #9692B0;
                        margin-left: 4px;
                    }
                }
                .price{
                    font-weight: 400;
                    font-size: 16px;
                    color: #5C596D;
                }
            }

            .del_price{
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 32px;

                p, span{
                    font-weight: 400;
                    font-size: 16px;
                    color: #5C596D;
                }
            }
            .total{
                display: flex;
                align-items: center;

                p, span{
                    font-weight: 400;
                    font-size: 20px;
                    color: #000;
                    margin-right: 32px;
                }
            }
        }
    }
}