
.category{
    width: 1290px;
    margin: 0 auto;
    margin-bottom: 96px;

    .category_list{
        display: flex;
        align-items: center;
        justify-content: space-between;
        list-style: none;

        .category_item{
            width: 300px;
            height: 232px;
            display: flex;
            flex-direction: column;
            padding: 16px;
            background: #F7F7F7;
            border-radius: 8px;
            transition: 0.3s all;

            
            img{
                margin-bottom: 16px;
            }
            
            span{
                font-weight: 700;
                font-size: 20px;
            }

            &:hover{
                border: 1px solid #B3C8FD;
                color: #B3C8FD;
            }
        }
    }
}