.delivery{
    width: 1296px;
    margin: 40px auto;

    h1{
        font-weight: 700;
        font-size: 24px;
        color: #5C596D;
        margin-bottom: 16px;
    }

    .info{
        width: 632px;
        font-weight: 400;
        font-size: 16px;
        margin-bottom: 40px;
    }

    ul{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;

        li{
            width: 31%;
            display: flex;
            flex-direction: column;
            margin-bottom: 40px;
            border: 2px solid #EAE9EF;
            border-radius: 8px;
            padding: 32px;

            p{
                margin-bottom: 22px;
                font-weight: 400;
                font-size: 16px;
            }
            span{
                font-weight: 400;
                font-size: 20px;
            }
        }
    }
}