.sing_in{
    width: 100%;
    height: 100vh;
    background-color: #0000004d;
    // position:relative;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index: 99;
    overflow-y: hidden;

    .wrapper{
        width: 538px;
        height: 420px;
        background-color: #fff;
        border-radius: 8px;
        text-align: center;
        padding: 32px 57px;
        position: relative;

        h1{
            font-weight: 700;
            color: #5C596D;
            margin-bottom: 16px;
            font-size: 24px;
        }
        p{
            margin-bottom: 24px;
            font-weight: 400;
            font-size: 16px;
            color: #5C596D;
        }
        input{
            width: 100%;
            padding: 14px;
            background: #F7F7F7;
            border-radius: 4px;
            font-weight: 400;
            font-size: 16px;
            border: none;
            margin-bottom: 12px;
        }
        
        .error{
            color: red;
            font-size: 14px;
            font-weight: 400;
        }

        button{
            width: 100%;
            background: #1B5BF7;
            border-radius: 4px;
            color: #fff;
            padding: 14px 0;
            border: none;
        }
        .popUp{
            margin-top: 24px;
        }

        .links{
            display: flex;
            align-items: center;
            justify-content: space-between;

            img{
                width: 200px;
                height: 48px;
                background-color: #F7F7F7;
                border-radius: 4px;
                padding: 10px 50px;
                object-fit: contain;
                margin-bottom: 24px;
            }
        }

        span{
            color: #5C596D;
            font-weight: 400;
            font-size: 16px;
        }
        .sam{
            color: #1B5BF7;
            font-weight: 400;
            font-size: 16px;
        }
        .close{
            position: absolute;
            top: 5%;
            right: 5%;
            font-size: 25px;
            font-weight: bold;
            width: 12px;
            height: 12px;
            background-color: #fff;
        }
    }
}